* {
  box-sizing: border-box; }

html {
  font-size: 1rem; }

h1, h2, h3, h4,
p, span, a {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  background-color: #f6f6f6; }

body,
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
dl, dd, ol, ul,
form, fieldset, legend,
figure,
table, th, td, caption {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, p {
  margin-bottom: 4; }

@font-face {
  font-family: modern-bold;
  font-weight: 400;
  src: url(../../../Fonts/TESCOModern-Bold-web.woff2) format("woff2"), url(../../../Fonts/TESCOModern-Bold-web.woff) format("woff"); }

@font-face {
  font-family: modern-regular;
  font-weight: 400;
  src: url(../../../Fonts/TESCOModern-Regular-web.woff2) format("woff2"), url(../../../Fonts/TESCOModern-Regular-web.woff) format("woff"); }

h1, h2, h2, h3 {
  font-family: 'modern-bold', Arial, Helvetica, sans-serif;
  color: #333333;
  font-weight: 400; }

p, a {
  font-family: 'modern-regular', Arial, Helvetica, sans-serif;
  color: #666666;
  font-weight: 400; }

p, span {
  color: #666666; }

a {
  text-decoration: none; }

h1 {
  font-size: 2rem;
  line-height: 2.25rem; }

h2 {
  font-size: 1.75rem;
  line-height: 2rem; }

h3 {
  font-size: 1.5rem;
  line-height: 1.75rem; }

h4 {
  font-size: 1.25rem;
  line-height: 1.5rem; }

a {
  text-decoration: none;
  color: #8f0023; }

.ddl-wrapper {
  max-width: 75rem;
  margin: 3.5rem auto;
  padding: 0 0.5rem; }

.flex-grp {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start; }

.rotate {
  transform: rotate(180deg);
  transform-origin: center;
  transition: transform .6s ease-out; }

[data-show="true"] {
  transform: none;
  margin-top: 0.75rem;
  max-height: 69rem;
  transition: max-height .6s ease-out, margin .6s ease; }
  [data-show="true"] .ddl-divider {
    opacity: 1;
    transition: opacity .7s ease-out; }

[data-show="false"] {
  max-height: 0;
  margin: 0;
  padding: 0 !important;
  overflow: hidden;
  transition: max-height .6s ease-out, margin .6s ease; }
  [data-show="false"] .input-grid {
    visibility: hidden; }
  [data-show="false"] .ddl-divider {
    opacity: 0;
    transition: opacity .1s ease-out; }

.ddl-btn {
  border-radius: 1.25rem;
  display: inline-block;
  font-size: 0.625rem;
  max-height: 2.5rem;
  min-width: 2.5rem;
  padding: 0.4375rem 1.25rem;
  text-align: center;
  transition: box-shadow .3s;
  vertical-align: middle;
  white-space: nowrap; }
  .ddl-btn-flld {
    background-color: #8f0023; }
    .ddl-btn-flld .ddl-lbl {
      color: #ffffff; }
    .ddl-btn-flld:hover, .ddl-btn-flld:focus {
      box-shadow: 0 0 0 4px rgba(143, 0, 35, 0.4);
      transition: box-shadow 0.3s ease-in-out; }
      .ddl-btn-flld:hover .ddl-lbl:after, .ddl-btn-flld:focus .ddl-lbl:after {
        border-bottom-color: #ffffff;
        transition: border-bottom-color 0.3s ease-in-out; }
  .ddl-btn-hllw {
    box-shadow: inset 0 0 0 2px #8f0023; }
    .ddl-btn-hllw .ddl-lbl {
      color: #8f0023; }
    .ddl-btn-hllw:hover, .ddl-btn-hllw:focus {
      box-shadow: inset 0 0 0 2px #8f0023, 0 0 0 4px rgba(143, 0, 35, 0.4); }
      .ddl-btn-hllw:hover .ddl-lbl:after, .ddl-btn-hllw:focus .ddl-lbl:after {
        border-bottom-color: #8f0023;
        transition: border-bottom-color 0.3s ease-in-out; }
  .ddl-btn .ddl-lbl {
    font-family: 'modern-bold', Arial, Helvetica, sans-serif; }

.ddl-btn-input {
  border: 0 solid transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  font-family: 'modern-bold', Arial, Helvetica, sans-serif; }

.ddl-rndl {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  position: relative;
  transition: transform .6s ease-out; }
  .ddl-rndl__icon {
    background: #8f0023;
    border-radius: 6.25rem;
    display: inline-block;
    transition: box-shadow .3s ease-in-out, transform .6s ease-in-out; }
  .ddl-rndl .ddl-svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.ddl-rndl__icon--sm {
  max-width: 1.5rem;
  min-height: 1.5rem;
  min-width: 1.5rem; }
  .ddl-rndl__icon--sm .ddl-xpnd {
    height: 1.5rem;
    width: 1.5rem; }
  .ddl-rndl__icon--sm .ddl-svg {
    width: 1.28125rem;
    height: 1.3125rem;
    padding: 4px;
    stroke-width: 2.4; }

.ddl-rndl__icon--md {
  max-width: 2rem;
  min-height: 2rem;
  min-width: 2rem; }
  .ddl-rndl__icon--md .ddl-xpnd {
    height: 2rem;
    width: 2rem; }
  .ddl-rndl__icon--md .ddl-svg {
    width: 1.28125rem;
    height: 1.3125rem;
    padding: 4px;
    stroke-width: 2.4; }

.ddl-rndl__icon--lrg {
  max-width: 2.5rem;
  min-height: 2.5rem;
  min-width: 2.5rem; }
  .ddl-rndl__icon--lrg .ddl-xpnd {
    height: 2.5rem;
    width: 2.5rem; }

.ddl-pairing {
  max-width: 50%; }

.ddl-divider {
  max-width: 75rem;
  min-height: 0.0625rem;
  background-color: #cccccc; }

.ddl-svg {
  display: inline-block;
  fill: none;
  stroke-width: 1.2;
  vertical-align: middle; }
  .ddl-svg--wht {
    stroke: #ffffff; }
  .ddl-svg--crmsn {
    stroke: #8f0023; }

.ddl-fltr {
  padding: 0.75rem;
  background: #ffffff; }
  .ddl-fltr:hover .ddl-rndl__icon, .ddl-fltr:focus .ddl-rndl__icon {
    box-shadow: 0 0 0 4px rgba(143, 0, 35, 0.4);
    transition: box-shadow 0.3s ease-in-out; }
  .ddl-fltr:hover .ddl-lbl:after, .ddl-fltr:focus .ddl-lbl:after {
    border-bottom-color: #8f0023;
    transition: border-bottom-color 0.3s ease-in-out; }

.input_group {
  border: none;
  padding: 0 0 12px; }
  .input_group .legend {
    font-family: 'modern-regular', Arial, Helvetica, sans-serif; }
  .input_group .form_field {
    position: relative; }
  .input_group--checkbox-radio .form_field {
    padding-left: 42px; }

.form_field {
  width: 100%;
  margin-top: 1.7rem;
  max-width: 480px;
  position: relative; }
  .form_field input {
    display: block; }
  .form_field .checkbox,
  .form_field .radio_button {
    position: absolute;
    top: 3px;
    left: 0; }

.label {
  display: inline-block;
  font-family: 'modern-regular', Arial, Helvetica, sans-serif;
  padding: 0.5rem 0;
  cursor: pointer; }

.checkbox {
  position: relative;
  display: inline-block;
  padding-left: 36px; }
  .checkbox input {
    width: 1.875rem;
    height: 1.875rem;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: 999;
    cursor: pointer; }
    .checkbox input:checked + .icon {
      background: #8f0023;
      border-color: #8f0023; }
    .checkbox input:checked + .icon svg {
      display: inline; }
    .checkbox input:hover + .icon {
      box-shadow: 0 0 0 4px rgba(241, 149, 171, 0.6);
      border-color: #8f0023;
      transition: box-shadow .3s ease-in-out, border-color .3s ease-in-out; }
  .checkbox .icon {
    display: block;
    background: #fff;
    width: 1.875rem;
    height: 1.875rem;
    line-height: 1.625rem;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #ccc;
    box-shadow: 0 0 0 transparent;
    text-align: center;
    transition: box-shadow .3s ease-in-out,border-color .3s ease-in-out; }
    .checkbox .icon svg {
      display: none;
      vertical-align: middle; }

.input-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(244px, 244px));
  max-width: 75%; }

.ddl-t1 {
  font-size: 1.5rem;
  line-height: 2rem; }

.ddl-t2 {
  font-size: 1.25rem;
  line-height: 1.75rem; }

.ddl-t3 {
  font-size: 1rem;
  line-height: 1.25rem; }

.ddl-lbl {
  display: inline-block;
  font-size: 1rem;
  line-height: 1.25rem;
  margin-right: 0.1875rem;
  position: relative;
  vertical-align: middle;
  white-space: nowrap; }
  .ddl-lbl:after {
    border-bottom: 0.0625rem solid transparent;
    bottom: 0;
    content: '';
    display: block;
    left: 0;
    position: absolute;
    transition: border 0.3s ease-in-out;
    width: 100%; }

.mb-16 {
  margin-bottom: 1rem !important; }

.mb-19 {
  margin-bottom: 1.1875rem !important; }

.mb-24 {
  margin-bottom: 1.5rem !important; }

.mb-36 {
  margin-bottom: 2.25rem !important; }

.mb-56 {
  margin-bottom: 3.5rem !important; }

.mr-8 {
  margin-right: 0.5rem !important; }

.mr-36 {
  margin-right: 2.25rem !important; }

.cl-crmsn {
  color: #8f0023 !important; }

.cl-wht {
  color: #ffffff !important; }

.ta-c {
  text-align: center !important; }

.hide {
  display: none !important; }
